@import"https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css";.main{background:url("../../../assets/background-imgs/main-back-hause.jpg") rgba(0,0,0,.6);background-position:center;background-repeat:no-repeat;background-size:cover;background-blend-mode:multiply;width:100%;height:100vh;background-attachment:fixed}.main .text-block{position:absolute;margin:15% 20%;width:45%;height:100%;text-align:center}.main .text-block h1,.main .text-block p{color:#f3e9db}.main .text-block h1{font-size:7rem}.main .text-block .line{border-bottom:3px solid #f3e9db;margin:4rem}.main .text-block p{font-size:5rem}.main .text-block span{font-family:Dubai,sans-serif;font-weight:bold}.main .page-buttons{display:flex;justify-content:center;align-items:flex-end;flex-direction:column;width:100%;height:100vh;gap:3rem;padding-right:2rem;padding-top:8rem}.main .page-buttons .button{background-color:rgba(154,165,170,.3);color:#9aa5aa;border-radius:5rem;border:none;padding:2rem;width:40rem;font-size:4rem}.main .page-buttons .button:hover{transition:background-color 1s;background-color:rgba(154,165,170,.5);color:#fff;cursor:pointer;display:flex;flex-direction:row;justify-content:center;align-items:center;gap:10%}.main .page-buttons .button:hover::after{content:"";font-family:FontAwesome !important;color:#fff;font-size:4rem}@media screen and (max-width: 1300px){.text-block{margin:17vh 0;width:100vw}.text-block h1{font-size:5rem}.page-buttons{justify-content:flex-end}}